* {
  margin: 0;
}

body {
  font-size: 24px;
}
h1{
  font-weight: bolder;
  font-family: 'Courier New', Courier, monospace;
}

h2 {
  font-size: 28px;
}
h3 {
  font-size:medium;
}

#drum-machine {
  margin: 15px;
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.wrapper {
  width: 100%;
  display: grid;
  padding: 10px;
  grid-gap: 15px;
  margin-top: 40px;
  border-radius: 15px;
  grid-template-columns: 1fr;
  background-image: linear-gradient(-225deg, #6495ED 35%, #9370DB 75%);
}

.keyboard {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(3, 1fr);

}

.keyboard .drum-pad {
  width: 100%;
  height: 80px;
  outline: none;
  font-size: 24px;
  cursor: pointer;
  border-radius: 10px;
  background-color: #ffffff;
  transition: all .3s ease-in-out
}

@media(min-width: 768px) {
  .wrapper {
    width: 680px;
  }
}

@media(min-width: 992px) {
  .wrapper {
    grid-template-columns: 1fr 1fr;
  }
}

.controle {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.controle>button,
.controle>input,
.controle>h2 {
  margin-bottom: 10px
}

.controle button {
  border: none;
  color: #ffffff;
  cursor: pointer;
  font-size: 20px;
  padding: 8px 10px;
  border-radius: 5px;
  background-color: #000000;
}